
import { PropType, computed, defineComponent } from "vue";
import { Objects } from "@/types/Objects";
import FilterDistricts from "./FilterDistricts.vue";
import FilterPrice from "./FilterPrice.vue";
import FilterLayouts from "./FilterLayouts.vue";
import FilterDateOfCompletion from "./FilterDateOfCompletion.vue";
import BroomIcon from "./Icons/BroomIcon.vue";
import { DataOfFiltres } from "@/types/DataOfFiltres";

export default defineComponent({
  name: "FilterForm",
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    dataOfFiltres: {
      type: Object as PropType<DataOfFiltres>,
      required: true,
    },
    clearStateDataOfFiltres: {
      type: Object as PropType<DataOfFiltres>,
      required: true,
    },
    districts: {
      type: Array as PropType<string[]>,
      required: true,
    },
    layouts: {
      type: Array as PropType<string[]>,
      required: true,
    },
    currencies: {
      type: Array as PropType<string[]>,
      required: true,
    },
    search: {
      type: String,
      required: true,
    },
  },
  components: {
    FilterDistricts,
    FilterPrice,
    FilterLayouts,
    FilterDateOfCompletion,
    BroomIcon,
  },
  emits: ["update:dataOfFiltres", "clearDataOfFiltres", "findObjects"],
  setup(props, { emit }) {
    const shouldShowClearButton = computed(() => {
      const actualDataOfFiltres = JSON.parse(
        JSON.stringify(props.dataOfFiltres)
      );
      const stateDataOfFiltresForClear = JSON.parse(
        JSON.stringify(props.clearStateDataOfFiltres)
      );

      delete actualDataOfFiltres.selectedDateOfCompletionRange;
      delete stateDataOfFiltresForClear.selectedDateOfCompletionRange;
      if (
        JSON.stringify(actualDataOfFiltres) ===
        JSON.stringify(stateDataOfFiltresForClear)
      ) {
        if (props.search) {
          return true;
        }
        return false;
      } else {
        return true;
      }
    });
    const updateDataOfFiltres = (
      key: keyof DataOfFiltres,
      value: string | number
    ) => {
      emit("update:dataOfFiltres", {
        ...props.dataOfFiltres,
        [key]: value,
      });
    };

    return {
      updateDataOfFiltres,
      shouldShowClearButton,
    };
  },
});
